import DataTable from "react-data-table-component";
import { Container, Button } from "react-bootstrap";
import { useAppContext } from "../context/AppContext";
import { downloadCSV } from "../utilities/utils";
import { useMemo } from "react";
const Datatables = (props) => {
  const { state,dispatch } = useAppContext()
  const { asset_type } = props;

  const data = state.data[asset_type].data;
  const columns = state.data[asset_type].columns;

  const paginationComponentOptions = {
    rowsPerPageText: "Rows per page",
    rangeSeparatorText: "of",
    selectAllRowsItem: false,
    selectAllRowsItemText: "",
  };

  const Export = ({ onExport }) => <Button onClick={e => onExport(e.target.value)}>Export</Button>;
  const actionsMemo = useMemo(() => <Export onExport={() => downloadCSV(data)} />, []);

  return (
    <Container style={{ marginTop: "0.5rem", marginBottom: "0.5rem" }}>
      <div style={{ marginBottom: "0.5rem"}}>
        <DataTable
          key={`datatable-${asset_type}`}
          columns={[...columns]}
          data={[...data]}
          pagination
          paginationComponentOptions={paginationComponentOptions}
          keyField="Dispatch ID"
          paginationPerPage={10}
          actions={actionsMemo}
          defaultSortFieldId="score"
          striped
          defaultSortAsc={false}
        />
      </div>
    </Container>
  );
};

export default Datatables;
