import { isCompositeComponent } from "react-dom/test-utils";

const reducer = (state, action) => {
  const { type, payload } = action;

  switch (type) {
    case "update_deep_field":
      let group = state[payload.group];
      group = { ...group, [payload.field]: payload.value };
      return {
        ...state,
        [payload.group]: group,
      };

    case "add_controller":
      const existingControls =
        state.data[payload.asset_type].controllers[payload.field].controls;

      state.data[payload.asset_type].controllers[payload.field].controls = {
        ...existingControls,
        [payload.option]: payload.value,
      };

      localStorage.setItem(`${payload.asset_type}-controllers`, JSON.stringify(state.data[payload.asset_type].controllers))

      return { ...state };

    case "update_controller_value":
      state.data[payload.asset_type].controllers[payload.field].controls[
        payload.option
      ] = payload.value;
      state.data[payload.asset_type].data = calculateScores(
        state.data[payload.asset_type].data,
        state.data[payload.asset_type].controllers
      );
      localStorage.setItem(`${payload.asset_type}-controllers`, JSON.stringify(state.data[payload.asset_type].controllers))
      return { ...state };

    case "remove_controller":
      //console.log(state);
      delete state.data[payload.asset_type].controllers[payload.field].controls[payload.option]

      localStorage.setItem(`${payload.asset_type}-controllers`, JSON.stringify(state.data[payload.asset_type].controllers))
      return {
        ...state
      };
  }
};

const calculateScores = (data, controllers) => {
  data.forEach((d) => {
    let score = 1;
    Object.keys(controllers).forEach((control) => {
      let value = d[control];      
      const multiplier = controllers[control].controls[value] || 1;
      d['score_' + control ] = parseFloat(multiplier);
      score = score * parseFloat(multiplier);
    });
    d.score = score;
    
  });

  return data;
};

export { calculateScores };

export default reducer;
