import React from "react";
import RangeSlider from "react-bootstrap-range-slider";
import { useAppContext } from "../context/AppContext";
import { Row, Col, Container, Button } from "react-bootstrap";
const Slider = (props) => {
  const context = useAppContext();
  const { state, dispatch } = context;
  const { asset_type, field, option } = props;

  const value = state.data[asset_type].controllers[field].controls[option];

  const handleChange = (e) => {

    dispatch({
      type: "update_controller_value",
      payload: {
        asset_type,
        field,
        option,
        value: e.target.value,
      },
    });
  };

  const handleClick = () => {
    dispatch({
      type: "remove_controller",
      payload: {
        asset_type,
        field,
        option,
      },
    });
  };
  return (
    <Container style={{ marginTop: "0.5rem" }}>
      <Row>
        <Col md={{ span: 3 }}>
          <h5 className="float-end">{option}</h5>
        </Col>
        <Col md={{ span: 4 }}>
          <RangeSlider
            value={value}
            onChange={handleChange}
            min={0}
            max={5}
            step={0.1}
            size={"sm"}
          />
        </Col>
        <Col md={{ span: 1 }}>
          <span className="float-start">{value}</span>
        </Col>
        <Col md={{ span: 1 }}>
          <Button variant="danger" size="sm" onClick={handleClick}>
            X
          </Button>
        </Col>
      </Row>
    </Container>
  );
};

export default Slider;
